@import url(https://fonts.googleapis.com/css?family=Spicy+Rice);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.title{
  font-family: 'Spicy Rice', cursive;
  margin-top: 10vh;
}

.homepage {
  display:flex;
  flex-direction: column;
  align-items: center;
  background-image: url(http://image.noelshack.com/fichiers/2018/44/3/1540968941-fondhome.jpg);
  background-size: cover;
  margin: auto;
  height: 640px;
  width: 360px;
  color: rgb(255, 255, 255);
  
}
.textHome{
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
}

.button {
  font-weight: bold;
  width: 180px;
  height: 60px;
  background-color: rgba(255, 255, 255,0.9);
  color: rgb(0, 0, 0);
  border-radius: 5px;
  font-size: 20px;
  text-align: center;
  margin-top: -1vh;
  margin-bottom: 1vh;
  
}
.button:focus{
  outline: none;
}
.candy {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 15vmin;
  width: 15vmin;
}

.container {
  position: absolute;
  right: -17vh;
  bottom: 3vh;
}
.row{
  margin-right: 9px !important;
  margin-left: -9px !important;
}
.container1 {
  margin: auto;
  margin-right: 1vw;
  margin-bottom: 18px;

}
.container2 {
  margin: auto;
}

#mapZone{
  position: absolute;
  left: 1vw;
  top:0.5vh;
  width: 98vw;
  height: 73.5vh; 
}
.backgroundReceiver{
  height: 640px;
  background-image: url(http://image.noelshack.com/fichiers/2018/44/3/1540974655-fondjoueur.png);
  background-size: cover;
}
#userInfo{
  width:90vw;
  height: 90vh;
  position: absolute;
  top: 5vh;
  left: 5vw;
  padding-top: 5vh;
}

#userInfo input{
  margin: 2vh 0 0 10vw;
  width: 70vw;
}

#buttonRow{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 3vh;
  margin-top: 3vh;
}

.mdp {
  color: red;
}
/* ----------profileGiver.css----------*/



/*-------- General Container -------*/

.profileGiver{
  margin-top: 2vh;
  display: flex;
  flex-direction: row;
}

/*-------- Avatar Profile -------*/

img{
  height: 30%;
  width: 30%;
}

/*-------- Coordinated Profile -------*/

.coordinated{
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  margin-top: 6vh;
  
}

.adress{
 
  font-size: 10px;
}
.name{
font-size: 15px;
margin-top: 4vh;
}

/*-------- General Button Profile -------*/

.buttonActionProfile{
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  margin-left: -36vw;
  }

/*--------  Button Changed Profile -------*/
.buttonChangeProfileInformation{
  width: 80px;
  height: 40px;
  background-color: darkgrey;
  color: white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 10px;
}
.buttonChangeProfileInformation:focus{
  outline: none;
  }
/*--------  Button Available -------*/
 
  .showAvailable{
    margin-top:2vh;
    margin-right: 8vw;
    margin-left: 4vw;
    font-size: 11px;
    font-weight: bold;
  }
  .availableButton{
    margin-top:1vh;
  }

.candySelect {
  display: flex;
  flex-direction: column;
  width: 250px;
  margin: auto;
}

.candySelect .btn {
    width: 150px;
    margin: auto;
    background-color: #854B86;
    color: white;
}

select {
  margin: 20px;
  font-size: 20px;
  border: 1px solid darkgray;
  margin: 40px 0px 20px 0px;
  text-align: center;
}

td {
  border: 1px solid #dddddd;
  background-color: white;
  border-radius: 10px;
  min-width: 150px; 
  text-align: left;
  padding: 8px;
  margin: 0px 10px 5px 0px;
}

.fckCandyTable {
  display: flex;
  justify-content: center;
  margin: 20px 0 10px 0;
}

.fckCandyTable .btn {
  margin: 0 0 0 10px;
  width: 50px;
  background-color: #854B86;
  color: white;
  font-weight: 700;
}
.giverInterface {
  background-position: bottom;
  width: 100vw;
  height: 100vh;
  background-size: 50vh;
  background-repeat: no-repeat;
  background-image:url("http://image.noelshack.com/fichiers/2018/44/3/1540978570-groupemonstres.png");
}

